@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,500;1,700&display=swap);

body {
  background-color: #C4CFD4;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App.modal-active {
  overflow: hidden;
  height: 100vh;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.post-container {
  max-width: 540px;
  margin: auto;
  padding: 20px;
}

.boton-donar {
  border: 2px solid #C5A77E;
  border-radius: 2em;
  padding: 15px 25px;
  color: #000;
  text-decoration: none;
  display: inline-block;
  margin-right: 5px;
  margin-left: 5px;
  transition: all 0.3s;
  font-weight: 700;
  margin-bottom: 30px;
}

.boton-donar:hover {
  color: #C5A77E;
}

.social-media {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 50px;
}

.social-media a {
  display: block;
  margin: 10px 20px;
  text-decoration: none;
  color: #517cc2;
  font-weight: 700;
  transition: all 0.3s;
}

.social-media a:hover {
  color: #C5A77E;
}
.formulario-container {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  padding: 70px 15px;
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
}

.formulario-container h2 {
  color: #fff;
}

.formulario-container__close {
  align-items: center;
  border: 0;
  top: 10px;
  background-color: rgba(0,0,0,0);
  color: #fff;
  cursor: pointer;
  display: flex;
  font-size: 30px;
  height: 60px;
  justify-content: center;
  position: absolute;
  right: 10px;
  width: 60px;
  transition: 0.3s;
}

.formulario-container__close:hover {
  opacity: 0.4;
}

form {
  align-items: flex-start;
  background-color: #fff;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.form-body {
  padding: 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.input-imagen {
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  width: 0.1px;
  z-index: -1;
}

.input-imagen__button {
  background-color: #000;
  cursor: pointer;
  display: flex;
  position: relative;
  min-height: 200px;
}

.input-imagen__button__overlay {
  align-items: center;
  background-color: rgba(37, 37, 37, 0.5);
  border-radius: 50%;
  bottom: 10px;
  display: flex;
  height: 60px;
  justify-content: center;
  position: absolute;
  right: 10px;
  width: 60px;
  z-index: 1;
}

.input-imagen__button__overlay img {
  height: 50px;
  width: 50px;
}

.input-nombre {
  background-color: #c5a77e;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  color: #fff;
  transform: translateY(-70%);
  width: auto;
  font-weight: 700;
}

input,
textarea {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  padding: 10px 15px;
  margin: 10px 0;
  border: 0;
}

input:focus,
textarea:focus {
  outline: none;
}

input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

textarea::-webkit-input-placeholder {
  font-weight: 500;
}

textarea:-ms-input-placeholder {
  font-weight: 500;
}

textarea::placeholder {
  font-weight: 500;
}

textarea {
  resize: none;
  margin-top: 0;
}

.form-button {
  background-color: #c5a77e;
  border: 0;
  color: #fff;
  cursor: pointer;
  font-weight: 500;
  padding: 10px 15px;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.3s;
}

.form-button:hover {
  background-color: #aa906b;
}

.form-button:disabled {
  cursor: no-drop;
  color: #ccc;
  background-color: #aaa;
}

.imagen-preview {
  max-width: 100%;
  max-height: 400px;
  object-fit: cover;
  object-position: center;
  width: 350px;
  transition: all 0.3s;
  opacity: 1;
}

.input-imagen__button:hover .imagen-preview {
  opacity: 0.8;
}

.captcha-container {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

.lds-heart {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 80px;
  height: 80px;
  transform: translate(-50%, -50%);
  transform: rotate(45deg);
  transform-origin: 40px 40px;
}
.lds-heart div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: #fff;
  animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}
.lds-heart div:after,
.lds-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: #fff;
}
.lds-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}
.lds-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}

@keyframes lds-heart {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}

header {
  padding: 10px 15px;
}

.boton-agregar {
  align-items: center;
  border-radius: 50%;
  border: 0;
  bottom: 20px;
  background-color: #C5A77E;
  color: #fff;
  cursor: pointer;
  display: flex;
  font-size: 60px;
  height: 80px;
  justify-content: center;
  position: fixed;
  right: 20px;
  width: 80px;
  transition: 0.3s;
}

.boton-agregar:hover {
  background-color: #806c52;
}

.boton-agregar__tooltip {
  pointer-events: none;
  background-color: #3d3d3d;
  border-radius: 2em 2em 0 2em;
  bottom: 85px;
  color: #fff;
  opacity: 0;
  padding: 10px 15px;
  position: fixed;
  right: 95px;
  transition: 0.3s;
}

.boton-agregar__tooltip.show {
  opacity: 1;
}
.header-link {
  border-radius: 2em;
  padding: 5px 10px;
  color: #000;
  text-decoration: none;
  display: inline-block;
  margin-right: 5px;
  margin-left: 5px;
  transition: all 0.3s;
  font-weight: 700;
}

/* #C5A77E*/

.header-link.active,
.header-link:hover  {
  color: #fff;
  background-color: #C5A77E;
}


@media only screen and (min-width: 768px) {
  .header-link {
    padding: 10px 15px;
  }
}
.grupo-tarjetas {
  padding: 20px 0px;
}

.tarjeta-peticion {
  background-color: #fff;
  border-radius: 16px;
  margin: 0 10px 20px;
  overflow: hidden;
  text-align: left;
}

.tarjeta-peticion__imagen {
  object-fit: cover;
  object-position: center;
  max-height: 380px;
  width: 100%;
}

.tarjeta-peticion__nombre {
  background-color: #c5a77e;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  color: #fff;
  display: inline-block;
  font-weight: 500;
  padding: 5px 15px;
  transform: translateY(-50%);
}

.tarjeta-peticion__body {
  padding: 0px 15px 20px;
}

.boton-random {
  align-items: center;
  border-radius: 50%;
  border: 0;
  bottom: 120px;
  background-color: #c5a77e;
  color: #fff;
  cursor: pointer;
  display: flex;
  font-size: 60px;
  height: 80px;
  justify-content: center;
  position: fixed;
  right: 20px;
  width: 80px;
  transition: 0.3s;
}

.boton-random:hover {
  background-color: #806c52;
}

.boton-random__tooltip {
  pointer-events: none;
  background-color: #3d3d3d;
  border-radius: 2em 2em 0 2em;
  bottom: 185px;
  color: #fff;
  opacity: 0;
  padding: 10px 15px;
  position: fixed;
  right: 95px;
  transition: 0.3s;
}

.boton-random__tooltip.show {
  opacity: 1;
}

.modal-peticion-aleatoria {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-peticion-aleatoria .tarjeta-peticion {
  max-width: 350px;
}

.modal__cerrar {
  position: absolute;
  align-items: center;
  border: 0;
  top: 10px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  cursor: pointer;
  display: flex;
  font-size: 30px;
  height: 60px;
  justify-content: center;
  position: absolute;
  right: 10px;
  width: 60px;
  transition: 0.3s;
}

